import React, { Component } from 'react';
import $ from 'jquery';
import SearchResults from './SearchResult.js';
import {Row, Col} from 'react-bootstrap';
class AppFrame extends Component {
    constructor(props) {
      super(props);
      this.state = {tags: '', showRes: false, imgs:[],
        currentToken: document.cookie.split('=')[1]
      };
      this.handleChange = this.handleChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleChange(event) {
      this.setState({tags: event.target.value});
    }
    handleSubmit(event) {
      this.setState({showRes: true});
      console.log(this.state.currentToken);
      $.ajax({
        url: 'http://ec2-54-200-112-229.us-west-2.compute.amazonaws.com/image/search',
        /*beforeSend: function(xhr) {
          xhr.setRequestHeader("Accept", "application/json");
          xhr.setRequestHeader("Content-Type", "application/json");
          xhr.setRequestHeader("Authorization", "JWT eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VybmFtZSI6ImF5YSIsInVzZXJfaWQiOjEsImVtYWlsIjoiZXpAeWhvLmNvbSIsImV4cCI6MTQ4NTUyNjMxNH0.af0u_v-mlEDqoqzGBIprUL1eQ82H6Zkr6gdVK5EGaww");
        },*/
        headers: {"Authorization": "JWT "+this.state.currentToken},
        data: {
          q: this.state.tags 
        },
        type: "GET",             // Type of request to be send, called as method
        crossDomain: true,
        success: function(data){   // A function to be called if request succeeds
          console.log("Returned some images");
          this.setState({imgs:data.images});
        }.bind(this)
      });
      event.preventDefault();
    }
    render() {
        let appDesc;
        if (!this.state.showRes) {
          appDesc=(
            <h3 className="img-search-desc">Enter a description of your image and we will find it for you</h3>
          )
        }
        else {
          appDesc=(
            <div></div>
          )
        }
        return(
          <div>
            <Row className="show-grid">
              <Col xs={3} md={2}></Col>
              <Col xs={8} md={8}>
                <div className="home-search-cont">
                <Row className="show-grid">
                <Col xs={3} md={2}></Col>
                <Col xs={8} md={8}>
                  <div className="search-desc">
                    <h1></h1>
                    <h1>Whimsy</h1>
                    {appDesc}
                  </div>
                </Col>
                <Col xs={3} md={2}></Col>
                </Row>
                <Row className="show-grid">
                  <Col xs={3} md={2}></Col>
                  <Col xs={8} md={8}>
                  <form onSubmit={this.handleSubmit}>
                      <input type="text" name="id" className="form-control" value={this.state.tags} onChange={this.handleChange} placeholder="Search" />
                  </form>
                  </Col>
                  <Col xs={3} md={2}></Col>
                </Row>
                
                <Row className="show-grid">
                  <Col xs={3} md={2}></Col>
                  <Col xs={8} md={8}>
                    <SearchResults shown={this.state.showRes} imgs={this.state.imgs} tag={this.state.tags}/>
                  </Col>
                  <Col xs={3} md={2}></Col>
                </Row>
                </div>
              </Col>
              <Col xs={3} md={2}>
              </Col>
            </Row>
          </div>
        );
    }
};

export default AppFrame;



