import React, { Component } from 'react';
import {Nav, Row, NavItem, Navbar, MenuItem, Button, ButtonToolbar, Dropdown, Glyphicon, Modal} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import $ from 'jquery';
//import logo from '../img-search-logo.png'

class AppNavbar extends Component {
    constructor(props) {
      super(props);
      this.state = {
        showModal:false,
        imgPath:"/home/aya/frontend-experiments/react-app/src/noimage.png",
        file:''
      };
      this.close = this.close.bind(this);
      this.open = this.open.bind(this);
      this.handleImgUpload = this.handleImgUpload.bind(this);
      this.handleImgChange = this.handleImgChange.bind(this);
    }
    close() {
      this.setState({showModal:false});
    }
    open() {
      this.setState({showModal:true});
    }
    handleImgUpload(e){
      e.preventDefault();
      $.ajax({
        url: "http://ec2-54-202-250-196.us-west-2.compute.amazonaws.com/image/trail", // Url to which the request is send
        type: "POST",             // Type of request to be send, called as method
        data: new FormData(e.target), // Data sent to server, a set of key/value pairs (i.e. form fields and values)
        contentType: false,       // The content type used when sending data to the server.
        cache: false,             // To unable request pages to be cached
        processData:false,        // To send DOMDocument or non processed data file it is set to false
        success: function(data){   // A function to be called if request succeeds
          console.log(new FormData(e.target));
        }
      });
    }
    handleImgChange(e){
      e.preventDefault();
      let reader = new FileReader();
      let targetFile = e.target.files[0];
      reader.onloadend = () => {
        this.setState({
          file: targetFile,
          imgPath: reader.result
        });
      }
      console.log(this.state.imgPath);
      var imagefile = targetFile.type;
      var match= ["image/jpeg","image/png","image/jpg"];
      if(!((imagefile===match[0]) || (imagefile===match[1]) || (imagefile===match[2]))){
        console.log("Please Select A valid Image File<");
        return false;
      }
      else{
        reader.readAsDataURL(targetFile);
      }
    }
    render() {
        return (
            <Navbar className="header">
                <Navbar.Header>
                    <Navbar.Brand>
                        <h2 className="logo">Whimsy</h2>
                    </Navbar.Brand>
                </Navbar.Header>
                <Nav>
                  <LinkContainer to={{ pathname: '/albums', query: { bar: 'baz' } }}>
                    <NavItem eventKey={1}><span className="left-nav">Albums</span></NavItem>
                  </LinkContainer>
                  <LinkContainer to={{ pathname: '/tagged', query: { bar: 'baz' } }}>
                    <NavItem eventKey={2}><span className="left-nav">Tagged</span></NavItem>
                  </LinkContainer>
                  <LinkContainer to={{ pathname: '/allimages', query: { bar: 'baz' } }}>
                    <NavItem eventKey={3}><span className="left-nav">All Images</span></NavItem>
                  </LinkContainer>
                </Nav>
                <Nav pullRight>
                    <NavItem eventKey={1}>
                    <Button onClick={this.open}>
                      Upload
                    </Button>
                    <Modal show={this.state.showModal} onHide={this.close}>
                     <Modal.Header closeButton>
                       <Modal.Title>Upload Image</Modal.Title>
                     </Modal.Header>
                     <Modal.Body>
                      <form id="uploadimage" onSubmit={this.handleImgUpload} action="" method="post" encType="multipart/form-data">
                        <Row id="image_preview" className="row">
                          <img id="previewing" src={this.state.imgPath} alt="preview"/>
                        </Row>
                        <div id="selectImage">
                          <label>Select Your Image</label><br/>
                          <input type="file" name="image" id="file" onChange={this.handleImgChange} required />
                          <Button type="submit" className="btn btn-primary" id="upload">Upload</Button>
                        </div>
                      </form>
                     </Modal.Body>
                     <Modal.Footer>
                       <Button onClick={this.close}>Close</Button>
                     </Modal.Footer>
                   </Modal>
                    </NavItem>
                    <NavItem eventKey={2}>
                      <ButtonToolbar>
                        <Dropdown id="dropdown-custom-1">
                          <Dropdown.Toggle>
                            <Glyphicon glyph="bell" />
                          </Dropdown.Toggle>
                          <Dropdown.Menu>
                            <MenuItem eventKey="1">Action</MenuItem>
                            <MenuItem divider/>
                            <MenuItem eventKey="2">Another action</MenuItem>
                          </Dropdown.Menu>
                        </Dropdown>
                      </ButtonToolbar>
                    </NavItem>
                    <NavItem eventKey={3}>
                      <ButtonToolbar>
                        <Dropdown id="dropdown-custom-1">
                          <Dropdown.Toggle>
                            <Glyphicon glyph="user" />
                          </Dropdown.Toggle>
                          <Dropdown.Menu>
                            <MenuItem eventKey="1">Profile Settings</MenuItem>
                            <MenuItem divider/>
                            <MenuItem eventKey="2">Account Settings</MenuItem>
                            <MenuItem divider/>
                            <MenuItem eventKey="3">Log out</MenuItem>
                          </Dropdown.Menu>
                        </Dropdown>
                      </ButtonToolbar>
                    </NavItem>
                </Nav>
            </Navbar>
        );
    }
}

export default AppNavbar;



