class SearchResults extends React.Component {

  render() {
    var url = "http://ec2-54-200-112-229.us-west-2.compute.amazonaws.com";
    console.log(this.props.imgs)
//    var tags = this.props.tag;
    // var imgs = this.props.imgs;
    var targetImgs = this.props.imgs;
    var test = '';

    // for (var i = 0; i < imgs.length; i++) {
    //   targetImgs.push(imgs[i].url_o);
    // }
    let imagesRes;
    if (this.props.shown) {
      if(targetImgs.length !== 0){
        imagesRes=(
          <Row className="img-row">
            {targetImgs.map(function(img, index){
              return (
                <ImageItem key={index} path={url+img}/>
              )
            })}
          </Row>
        )
      }
      else{
        imagesRes=(
          <h1>No matched discription</h1>
        )
      }
    }
    else{
      imagesRes=(<Row></Row>)
    }
    return (
      <div>
        {test}
        {imagesRes}
      </div>
    );
  }
}

export default SearchResults;



